.btn--primary {
  background-image: linear-gradient(113deg, #6a36ff, #ac5fe6);
  color: #ffffff;
  transition: 0.2s;
}
.btn--primary:hover {
  background-image: none;
  background-color: #ac5fe6;
  color: #ffffff;
}
.btn--secondary-purple {
  color: transparent;
  background-image: linear-gradient(113deg, #6a36ff, #ac5fe6);
  -webkit-background-clip: text;
  background-clip: text;
  position: relative;
}
.btn--secondary-purple .btn__icon {
  background-color: none;
  background-image: linear-gradient(113deg, #6a36ff, #ac5fe6);
}
.btn--secondary-purple::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 10px;
  border: 1px solid transparent;
  background: linear-gradient(113deg, #6a36ff, #ac5fe6) border-box;
  -webkit-mask: linear-gradient(#ffffff 0 0) padding-box, linear-gradient(#ffffff 0 0);
  -webkit-mask-composite: xor;
  -mask-composite: exclude;
}
.btn--secondary-purple:hover {
  border: none;
  background: none;
  background-color: #ac5fe6;
  color: #ffffff;
}
.btn--secondary-purple:hover::before {
  inset: unset;
}
.btn--secondary-purple:hover .btn__icon {
  background-image: none;
  background-color: #ffffff;
}
.btn--secondary-white {
  color: #ffffff;
  outline: 1px solid #ffffff;
}
.btn--secondary-white:hover {
  outline: none;
  background-image: none;
